import { Box, Container, ButtonGroup, Grid, makeStyles, Button, } from '@material-ui/core'
import React from 'react'
import Navbarr from './navbar'

import InstagramIcon from '@material-ui/icons/Instagram';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';


const useStyles = makeStyles(theme => ({

    page: {
        width: '100%',
    },
    title: {
        flexGrow: 1,
        padding: theme.spacing(3, 3, 3, 10),
    },
    navbutton: {
        padding: theme.spacing(0, 10, 0)
    },
    dark:{
        backgroundColor: '#212121',

    }
}))

export default function Layout({ children }) {
    const classes = useStyles()

    return (
        <div>
            <Navbarr />


            <div className={classes.page}>

                {children}
            </div>
            <Box className={classes.dark} p={2} >
                <Container  maxWidth="lg">
                    <Grid container>
                        <Grid item lg={8} xs={12}>
                            <ButtonGroup color="secondary" aria-label="outlined secondary button group">
                                <Button>Terms of Use</Button>
                                <Button>Jersykatz</Button>
                                <Button>Klasses</Button>
                                <Button>Blogs</Button>
                                <Button>Subscribe</Button>
                            </ButtonGroup>
                        </Grid>
                        <Grid ms="auto" item>
                            <Box ms="auto">
                                <Button color="secondary"><LinkedInIcon /></Button>
                                <Button color="secondary"><FacebookIcon /></Button>
                                <Button color="secondary"><TwitterIcon /></Button>
                                <Button color="secondary"><InstagramIcon /></Button>

                            </Box>
                        </Grid>
                    </Grid>
                </Container>
            </Box>
        </div>
    )
}
